.section-7 {
    position: relative;
    height: 200vh; // ili više ako želiš dugačak efekat
  
    &--base {
      position: sticky;
      top: 0;
      height: 100vh;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #0e0e0e;
      z-index: 1;
  
      h1 {
        color: white;
        font-size: 4rem;
      }
    }
  
    &--overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 200vh;
      z-index: 2;
      pointer-events: none; // ne ometa korisnika
    }
  }
  