.scroll-narrative {
    height: 100vh;
    transition: background-color 0.1s ease;
    display: flex;
    align-items: center;
    justify-content: center;

    .backgroundImg {
        position: absolute;
        z-index: 0;
        width: 70%;
        height: 100%;
        right: 0;
        top: 50%;
        transform: translate(-0%, -50%);
        opacity: .35;
        background-blend-mode: multiply;
        object-fit: cover;
    }
}