
.scroll-zone {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: absolute;
  padding: 0 5vw;

  .text-current {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 70%;
  }

  .text-current h2 {
    font-size: 80px;
    padding-bottom: 10%;
  }

  .text-current p {
    font-size: 22px;
    float: right;
    font-weight: 200;
  }

  .text-current-withNoHeading {
    font-size: 22px;
    font-weight: 200;
  }

  .zone-img-box {
    position: absolute;
    right: 0;
    width: 70%;
    display: flex;
    justify-content: flex-end;
    img {
      max-width: 70%;
      max-height: 80vh;
      object-fit: contain;
      z-index: 30;
    }
  }

}

.scroll-box {
  width: 30%;
  position: relative; 
  overflow: hidden; 
  display: flex; 
  align-items: center; 
  height: 100%;
}